<template>
	<div>
		<nav class="navbar navbar-transparent navbar-absolute">
			<div class="container">
				<div class="navbar-header">
					<button type="button" class="navbar-toggle"
							data-toggle="collapse"
							data-target="#navigation-example-2"
							@click="toggleNavbar">
						<span class="sr-only">Переключить навигацию</span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
					</button>
					<span class="navbar-brand">Reagent Store</span>
				</div>
				<div class="collapse navbar-collapse">
					<ul class="nav navbar-nav navbar-right">
						<router-link to="/login" tag="li"><a>Вход</a></router-link>
					</ul>
				</div>
			</div>
		</nav>
		<div class="wrapper wrapper-full-page">
			<div class="full-page login-page" data-color="blue" data-image="static/img/background/background-2.jpg">
				<!--   you can change the color of the filter page using: data-color="blue | azure | green | orange | red | purple" -->
				<div class="content">
					<div class="container">
						<div class="row">
							<div class="col-md-4 col-sm-6 col-md-offset-4 col-sm-offset-3">
							</div>
						</div>
					</div>
				</div>
				<footer class="footer footer-transparent">
					<div class="container"><div class="copyright">&copy; 2019, NSK</div></div>
				</footer>
				<div class="full-page-background" style="background-image: url(static/img/background/background-2.jpg) "></div>
			</div>
		</div>
		<div class="collapse navbar-collapse off-canvas-sidebar">
			<ul class="nav nav-mobile-menu">
				<router-link to="/login" tag="li"><a>Вход</a></router-link>
			</ul>
		</div>
	</div>
</template>
<script>
	import swal from 'sweetalert2'
	export default {
		created () {
			this.model.token = this.getToken()
			if (this.model.token !== '' && this.model.token.match(/[a-zA-Z0-9_-]+/)) {
				this.activate()
			} else {
				this.activationError()
			}
		},
		data () {
			return {
				model: {
					token: ''
				}
			}
		},
		methods: {
			toggleNavbar () {
				document.body.classList.toggle('nav-open')
			},
			closeMenu () {
				document.body.classList.remove('nav-open')
				document.body.classList.remove('off-canvas-sidebar')
			},
			validate () {
				this.$validator.validateAll().then(isValid => {
					if (isValid) {
						this.register()
					}
				})
			},
			getToken () {
				return this.$route.params.token
			},
			activationError (defaultText = '') {
				swal({
					title: 'Ошибка активации',
					text: (defaultText === '') ? 'Не корректная ссылка' : defaultText,
					type: 'error'
				})
			},
			activate () {
				var self = this
				swal({
					title: 'Активация аккаунта',
					text: 'Пожалуйста, подождите...',
					onOpen: () => {
						swal.showLoading()
					},
					showConfirmButton: false,
					allowOutsideClick: false,
					allowEscapeKey: false,
					allowEnterKey: false
				})
				this.$store.dispatch('userActivate', {
					account: this.model,
					callback: function (data) {
						swal({
							title: 'Ваш аккаунт был успешно активирован',
							text: 'Для дальнейшей работы необходимо авторизироваться в системе',
							type: 'success',
							showConfirmButton: true
						}).then(function (result) { self.$router.push({ path: '/login' }) })
					},
					successFalse: function (data) {
						self.activationError(data.message)
					}
				})
			}
		},
		beforeDestroy () {
			this.closeMenu()
		},
		watch: {
			activateToken: function (value) {
				this.setDefaultFilter()
			}
		}
	}
</script>
<style>
</style>



